<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">行政管理</div>
      </div>
      <div class="content-body">
        <div class="createapply-home-icongroup" v-for="item in applyTypelist" v-if="item.child.length!=0">
          <div class="createapply-home-icongroup-title">
            {{item.name}}
          </div>
          <div class="createapply-home-icon-itemlist">
            <div class="createapply-home-iconitem" @click="gotoCreateApply(item1)" v-for="item1 in item.child">
              <img :src="item1.icon" class="">
              <div>{{item1.name}}</div>
              <span v-if="item1.num>0"></span>
            </div>
          </div>
        </div>
        <div class="createapply-home-icongroup" v-if="permissionsManageList.child.length!=0">
          <div class="createapply-home-icongroup-title">
            {{permissionsManageList.name}}
          </div>
          <div class="createapply-home-icon-itemlist">
            <div class="createapply-home-iconitem" @click="goPermissionsMoudle(item1)" v-for="item1 in permissionsManageList.child">
              <img :src="'../static/images/adminhomepage_'+item1.name+'.png'" class="">
              <div>{{item1.node_name}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//审批流程状态
import {
  dialogConfirm,
  compareJson,
  dialogConfirmHasbtn,
  getStore
} from '../../config/mUtils'
import {
  getApplyType,
  getUserMenu
} from '../../config/api'
export default {
  data() {
    return {
      applyTypelist: [{
        name: '出勤休假',
        isPermissions: false,
        child: []
      }, {
        name: '用印申请',
        isPermissions: false,
        child: []
      }, {
        name: '签字申请',
        isPermissions: false,
        child: []
      }, {
        name: '被投',
        isPermissions: false,
        child: []
      }],
      permissionsManageList: {
        name: '系统管理',
        isPermissions: true,
        child: []
      }
    }
  },
  created() {
    //获取审批类型
    getApplyType({}).then(res => {
      if (res.data.msgNo == 1) {
        let tl = res.data.payload;
        tl.forEach(item => {
          item.icon = '../../static/images' + item.icon;
          this.applyTypelist[parseInt(item.type) - 1].child.push(item);
        })
      }
    })
    //获取用户权限
    getUserMenu({
      user_id: getStore('htcx_uid'),
      specMenuId: 20
    }).then(res => {
      if (res.data.msgNo == 1) {
        this.permissionsManageList.child = res.data.payload;
      } else {
        this.permissionsManageList.child = []
      }
    })
  },
  components: {},
  computed: {},
  mounted() {},
  methods: {
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.realList = to.meta.routeList;
      });
    },
    gotoCreateApply(item) {
      this.$router.push({
        name: 'applyList',
        query: {
          template_id: item.id,
          aType: item.type,
          aName: item.name,
          from: 3
        }
      })

    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    },
    goPermissionsMoudle(item) {
      this.$router.push({
        name: item.name,
        query: {
          from: 3
        }
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.personal-content .content-body {
  position: absolute;
  border: 1px solid #E4E4E4;
  padding: 18px 16px 16px;
  left: 36px;
  right: 36px;
  top: 80px;
  bottom: 18px;
  overflow: auto;
}

</style>



